import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../app/store';
import { register, clearError } from './authSlice';
import Input from '../../components/common/Input';
import Button from '../../components/common/Button';
import Layout from '../../components/common/Layout';

const Register: React.FC = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
    role: 'student' as 'student' | 'instructor' | 'admin',
    phoneNumber: '',
    address: '',
    organization: '',
    jobTitle: '',
    bio: '',
  });
  
  const [formErrors, setFormErrors] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
    phoneNumber: '',
  });
  
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { isLoading, error, isAuthenticated } = useAppSelector((state) => state.auth);
  
  useEffect(() => {
    //     
    if (isAuthenticated) {
      navigate('/dashboard');
    }
    
    //     
    return () => {
      dispatch(clearError());
    };
  }, [isAuthenticated, navigate, dispatch]);
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: name === 'role' ? value as 'student' | 'instructor' | 'admin' : value,
    });
  };
  
  const validateForm = (): boolean => {
    let valid = true;
    const newErrors = {
      email: '',
      password: '',
      confirmPassword: '',
      firstName: '',
      lastName: '',
      phoneNumber: '',
    };
    
    //  
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!formData.email.trim()) {
      newErrors.email = ' ';
      valid = false;
    } else if (!emailRegex.test(formData.email)) {
      newErrors.email = '   ';
      valid = false;
    }
    
    //  
    if (!formData.password) {
      newErrors.password = ' ';
      valid = false;
    } else if (formData.password.length < 6) {
      newErrors.password = '  6  ';
      valid = false;
    }
    
    //   
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
      valid = false;
    } else if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
      valid = false;
    }
    
    //  
    if (!formData.firstName.trim()) {
      newErrors.firstName = ' ';
      valid = false;
    }
    
    if (!formData.lastName.trim()) {
      newErrors.lastName = ' ';
      valid = false;
    }
    
    //   ()
    if (formData.phoneNumber && !/^[0-9-+\s()]+$/.test(formData.phoneNumber)) {
      newErrors.phoneNumber = '   ';
      valid = false;
    }
    
    setFormErrors(newErrors);
    return valid;
  };
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      try {
        console.log('Submitting registration data:', {
          email: formData.email,
          password: '***HIDDEN***',
          first_name: formData.firstName,
          last_name: formData.lastName,
        });
        
        await dispatch(register({
          email: formData.email,
          password: formData.password,
          password_confirm: formData.confirmPassword,
          first_name: formData.firstName,
          last_name: formData.lastName,
          role: formData.role,
          phone_number: formData.phoneNumber,
          address: formData.address,
          organization: formData.organization,
          job_title: formData.jobTitle,
          bio: formData.bio,
        })).unwrap();
        
        //      
        alert('  .   .');
        navigate('/login');
      } catch (err: any) {
        console.error('Registration failed', err);
        //   authSlice extraReducers 
        //      Redux store error   UI 
      }
    }
  };
  
  return (
    <Layout>
      <div className="max-w-md mx-auto">
        <h1 className="text-2xl font-bold mb-6 text-center"></h1>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <Input
              id="firstName"
              name="firstName"
              type="text"
              label=""
              value={formData.firstName}
              onChange={handleChange}
              placeholder=""
              error={formErrors.firstName}
              fullWidth
            />
            
            <Input
              id="lastName"
              name="lastName"
              type="text"
              label=""
              value={formData.lastName}
              onChange={handleChange}
              placeholder=""
              error={formErrors.lastName}
              fullWidth
            />
          </div>
          
          
          <Input
            id="email"
            name="email"
            type="email"
            label=""
            value={formData.email}
            onChange={handleChange}
            placeholder="example@example.com"
            error={formErrors.email}
            fullWidth
          />
          
          <Input
            id="password"
            name="password"
            type="password"
            label=""
            value={formData.password}
            onChange={handleChange}
            placeholder=""
            error={formErrors.password}
            fullWidth
          />
          
          <Input
            id="confirmPassword"
            name="confirmPassword"
            type="password"
            label=" "
            value={formData.confirmPassword}
            onChange={handleChange}
            placeholder=" "
            error={formErrors.confirmPassword}
            fullWidth
          />
          
          {/*   */}
          <div className="bg-gray-50 p-4 rounded-lg">
            <label className="block text-sm font-medium text-gray-700 mb-3">
                 *
            </label>
            
            <div className="space-y-3">
              {/*   */}
              <div
                className={`relative flex cursor-pointer rounded-lg border p-4 transition-all ${
                  formData.role === 'student'
                    ? 'border-indigo-600 bg-indigo-50 ring-2 ring-indigo-600'
                    : 'border-gray-300 bg-white hover:bg-gray-50'
                }`}
                onClick={() => setFormData({ ...formData, role: 'student' })}
              >
                <div className="flex h-5 items-center">
                  <input
                    id="role-student"
                    name="role"
                    type="radio"
                    value="student"
                    checked={formData.role === 'student'}
                    onChange={handleChange}
                    className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300"
                  />
                </div>
                <div className="ml-3 flex-1">
                  <div className="flex items-center">
                    <span className="text-2xl mr-2"></span>
                    <label htmlFor="role-student" className="text-base font-medium text-gray-900 cursor-pointer">
                       
                    </label>
                  </div>
                  <p className="text-sm text-gray-600 mt-1">
                        .    .
                  </p>
                </div>
              </div>

              {/*   */}
              <div
                className={`relative flex cursor-pointer rounded-lg border p-4 transition-all ${
                  formData.role === 'instructor'
                    ? 'border-indigo-600 bg-indigo-50 ring-2 ring-indigo-600'
                    : 'border-gray-300 bg-white hover:bg-gray-50'
                }`}
                onClick={() => setFormData({ ...formData, role: 'instructor' })}
              >
                <div className="flex h-5 items-center">
                  <input
                    id="role-instructor"
                    name="role"
                    type="radio"
                    value="instructor"
                    checked={formData.role === 'instructor'}
                    onChange={handleChange}
                    className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300"
                  />
                </div>
                <div className="ml-3 flex-1">
                  <div className="flex items-center">
                    <span className="text-2xl mr-2"></span>
                    <label htmlFor="role-instructor" className="text-base font-medium text-gray-900 cursor-pointer">
                       
                    </label>
                  </div>
                  <p className="text-sm text-gray-600 mt-1">
                         . ( ,   )
                  </p>
                </div>
              </div>
            </div>
          </div>
          
          {/*    */}
          <div className="border-t border-gray-200 pt-6">
            <h3 className="text-lg font-medium text-gray-900 mb-4"> </h3>
            <p className="text-sm text-gray-600 mb-4">  ,     .</p>
            
            <div className="space-y-4">
              <Input
                id="phoneNumber"
                name="phoneNumber"
                type="tel"
                label=""
                value={formData.phoneNumber}
                onChange={handleChange}
                placeholder="010-1234-5678"
                error={formErrors.phoneNumber}
                fullWidth
              />
              
              <Input
                id="address"
                name="address"
                type="text"
                label=""
                value={formData.address}
                onChange={handleChange}
                placeholder=" ..."
                fullWidth
              />
              
              <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                <Input
                  id="organization"
                  name="organization"
                  type="text"
                  label=" "
                  value={formData.organization}
                  onChange={handleChange}
                  placeholder="  "
                  fullWidth
                />
                
                <Input
                  id="jobTitle"
                  name="jobTitle"
                  type="text"
                  label=""
                  value={formData.jobTitle}
                  onChange={handleChange}
                  placeholder=",  "
                  fullWidth
                />
              </div>
              
              <div>
                <label htmlFor="bio" className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <textarea
                  id="bio"
                  name="bio"
                  rows={3}
                  value={formData.bio}
                  onChange={handleChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                  placeholder="  ..."
                />
              </div>
            </div>
          </div>
          
          <div className="flex items-center">
            <input
              id="terms"
              name="terms"
              type="checkbox"
              className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
              required
            />
            <label htmlFor="terms" className="ml-2 block text-sm text-gray-700">
              <span>
                <Link to="/terms" className="text-indigo-600 hover:text-indigo-500">
                  
                </Link>
                {' '}{' '}
                <Link to="/privacy" className="text-indigo-600 hover:text-indigo-500">
                  
                </Link>
                 
              </span>
            </label>
          </div>
          
          <Button
            type="submit"
            variant="primary"
            isLoading={isLoading}
            fullWidth
          >
            
          </Button>
          
          <div className="text-center mt-4">
            <span className="text-gray-600">  ?</span>{' '}
            <Link to="/login" className="text-indigo-600 hover:text-indigo-500">
              
            </Link>
          </div>
        </form>
      </div>
    </Layout>
  );
};

export default Register;