import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '../../app/store';
import Layout from '../../components/common/Layout';
import Button from '../../components/common/Button';
import Input from '../../components/common/Input';
import { supabase } from '../../lib/supabase';
import { setupStorageBuckets } from '../../utils/storageSetup';
import StorageSetupGuide from '../../components/admin/StorageSetupGuide';

interface CourseForm {
  title: string;
  description: string;
  category: string;
  level: 'beginner' | 'intermediate' | 'advanced';
  price: number;
  thumbnail: File | null;
  tags: string[];
}

interface Module {
  id: string;
  title: string;
  description: string;
  order: number;
  chapters: Chapter[];
}

interface Chapter {
  id: string;
  title: string;
  description: string;
  content_type: 'video' | 'text' | 'pdf' | 'quiz';
  video_file: File | null;
  video_url: string;
  text_content: string;
  duration_minutes: number;
  order: number;
}

const CourseCreator: React.FC = () => {
  const navigate = useNavigate();
  const { user, isAuthenticated } = useAppSelector((state) => state.auth);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [currentStep, setCurrentStep] = useState(1);

  //   
  useEffect(() => {
    if (!isAuthenticated || !user || user.role !== 'admin') {
      alert('  .');
      navigate('/login');
      return;
    }
  }, [isAuthenticated, user, navigate]);
  
  const [courseForm, setCourseForm] = useState<CourseForm>({
    title: '',
    description: '',
    category: '',
    level: 'beginner',
    price: 0,
    thumbnail: null,
    tags: []
  });

  const [modules, setModules] = useState<Module[]>([]);
  const [newTag, setNewTag] = useState('');
  const [thumbnailPreview, setThumbnailPreview] = useState<string>('');
  const [categories, setCategories] = useState<{ id: string; name: string; icon?: string }[]>([]);
  const [showStorageGuide, setShowStorageGuide] = useState(false);

  //  
  useEffect(() => {
    const initializeComponent = async () => {
      // 1. Storage   /
      await setupStorageBuckets();
      
      // 2.  
      try {
        const { data, error } = await supabase
          .from('categories')
          .select('id, name, icon')
          .order('name');

        if (error) throw error;
        setCategories(data || []);
      } catch (error) {
        console.error('  :', error);
        //   
        setCategories([
          { id: 'temp-1', name: 'AI/' },
          { id: 'temp-2', name: ' ' },
          { id: 'temp-3', name: '' }
        ]);
      }
    };

    initializeComponent();
  }, []);

  const handleCourseFormChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setCourseForm(prev => ({
      ...prev,
      [name]: name === 'price' ? parseFloat(value) || 0 : value
    }));
  };

  const handleThumbnailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setCourseForm(prev => ({ ...prev, thumbnail: file }));
      
      //  
      const reader = new FileReader();
      reader.onload = (e) => {
        setThumbnailPreview(e.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const addTag = () => {
    if (newTag.trim() && !courseForm.tags.includes(newTag.trim())) {
      setCourseForm(prev => ({
        ...prev,
        tags: [...prev.tags, newTag.trim()]
      }));
      setNewTag('');
    }
  };

  const removeTag = (index: number) => {
    setCourseForm(prev => ({
      ...prev,
      tags: prev.tags.filter((_, i) => i !== index)
    }));
  };

  const addModule = () => {
    const newModule: Module = {
      id: `module_${Date.now()}`,
      title: '',
      description: '',
      order: modules.length + 1,
      chapters: []
    };
    setModules(prev => [...prev, newModule]);
  };

  const updateModule = (moduleId: string, field: keyof Module, value: any) => {
    setModules(prev => prev.map(module => 
      module.id === moduleId ? { ...module, [field]: value } : module
    ));
  };

  const removeModule = (moduleId: string) => {
    setModules(prev => prev.filter(module => module.id !== moduleId));
  };

  const addChapter = (moduleId: string) => {
    const newChapter: Chapter = {
      id: `chapter_${Date.now()}`,
      title: '',
      description: '',
      content_type: 'video',
      video_file: null,
      video_url: '',
      text_content: '',
      duration_minutes: 0,
      order: 1
    };

    setModules(prev => prev.map(module => {
      if (module.id === moduleId) {
        return {
          ...module,
          chapters: [...module.chapters, { ...newChapter, order: module.chapters.length + 1 }]
        };
      }
      return module;
    }));
  };

  const updateChapter = (moduleId: string, chapterId: string, field: keyof Chapter, value: any) => {
    setModules(prev => prev.map(module => {
      if (module.id === moduleId) {
        return {
          ...module,
          chapters: module.chapters.map(chapter =>
            chapter.id === chapterId ? { ...chapter, [field]: value } : chapter
          )
        };
      }
      return module;
    }));
  };

  const removeChapter = (moduleId: string, chapterId: string) => {
    setModules(prev => prev.map(module => {
      if (module.id === moduleId) {
        return {
          ...module,
          chapters: module.chapters.filter(chapter => chapter.id !== chapterId)
        };
      }
      return module;
    }));
  };

  //    
  const getVideoDuration = (file: File): Promise<number> => {
    return new Promise((resolve, reject) => {
      const video = document.createElement('video');
      video.preload = 'metadata';
      
      video.onloadedmetadata = () => {
        window.URL.revokeObjectURL(video.src);
        const durationInMinutes = Math.ceil(video.duration / 60);
        resolve(durationInMinutes);
      };
      
      video.onerror = () => {
        window.URL.revokeObjectURL(video.src);
        reject(new Error('    .'));
      };
      
      video.src = URL.createObjectURL(file);
    });
  };

  const handleVideoUpload = async (moduleId: string, chapterId: string, file: File) => {
    try {
      console.log('   :', {
        fileName: file.name,
        fileSize: file.size,
        fileType: file.type,
        moduleId,
        chapterId
      });

      // 1.   
      const maxSize = 500 * 1024 * 1024; // 500MB
      const allowedTypes = ['video/mp4', 'video/webm', 'video/ogg', 'video/avi', 'video/mov', 'video/quicktime'];
      
      if (file.size > maxSize) {
        throw new Error(`   .  500MB  . (: ${Math.round(file.size / 1024 / 1024)}MB)`);
      }
      
      if (!allowedTypes.includes(file.type)) {
        throw new Error(`   .  : MP4, WebM, OGG, AVI, MOV (: ${file.type})`);
      }

      // 2.   
      console.log('    ...');
      const duration = await getVideoDuration(file);
      console.log(`  : ${duration}`);
      
      // 3.   
      const fileExt = file.name.split('.').pop()?.toLowerCase();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `courses/${moduleId}/${chapterId}/${fileName}`;

      console.log('  :', filePath);
      
      // 4. Storage   ( chapter-files )
      let bucketName = 'chapter-files'; // :   
      
      try {
        //     ( )
        const { data: buckets, error: listError } = await supabase.storage.listBuckets();
        
        if (!listError && buckets) {
          console.log('  :', buckets.map(b => `${b.name}(${b.public ? 'public' : 'private'})`));
          
          //    : chapter-files > course-videos
          const availableBucket = buckets.find(bucket => 
            bucket.name === 'chapter-files' || bucket.name === 'course-videos'
          );
          
          if (availableBucket) {
            bucketName = availableBucket.name;
            console.log('   :', bucketName, '(public:', availableBucket.public + ')');
            
            if (!availableBucket.public) {
              console.warn('  private . Public    .');
            }
          } else {
            console.warn('      chapter-files ');
          }
        } else {
          console.warn('     ( ), chapter-files  :', listError?.message);
        }
      } catch (error) {
        console.warn('    , chapter-files  :', error);
      }
      
      console.log('  :', bucketName);
      
      // 5. Supabase Storage  
      console.log('   ... (:', bucketName + ')');
      const { data, error } = await supabase.storage
        .from(bucketName)
        .upload(filePath, file, {
          cacheControl: '3600',
          upsert: false
        });

      if (error) {
        console.error(' Supabase  :', error);
        throw new Error(` : ${error.message}`);
      }

      console.log('  :', data);

      // 6.   URL 
      const { data: { publicUrl } } = supabase.storage
        .from(bucketName)
        .getPublicUrl(filePath);

      console.log('   URL:', publicUrl);

      // 7. URL  
      try {
        const testResponse = await fetch(publicUrl, { method: 'HEAD' });
        if (!testResponse.ok) {
          console.warn('  URL   :', testResponse.status);
        } else {
          console.log(' URL   ');
        }
      } catch (urlError) {
        console.warn(' URL  :', urlError);
      }

      // 8.   ( )
      updateChapter(moduleId, chapterId, 'video_url', publicUrl);
      updateChapter(moduleId, chapterId, 'duration_minutes', duration);
      updateChapter(moduleId, chapterId, 'video_file', file);

      alert(`  !\n- : ${file.name}\n- : ${duration}\n- : ${Math.round(file.size / 1024 / 1024)}MB`);
    } catch (error: any) {
      console.error('   :', error);
      alert('  :\n\n' + error.message);
    }
  };

  const uploadThumbnail = async (): Promise<string | null> => {
    if (!courseForm.thumbnail) return null;

    try {
      const fileExt = courseForm.thumbnail.name.split('.').pop();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `course_${Date.now()}/${fileName}`;

      const { data, error } = await supabase.storage
        .from('course-thumbnails')
        .upload(filePath, courseForm.thumbnail);

      if (error) throw error;

      const { data: { publicUrl } } = supabase.storage
        .from('course-thumbnails')
        .getPublicUrl(filePath);

      return publicUrl;
    } catch (error: any) {
      console.error('  :', error);
      throw error;
    }
  };

  // slug  
  const generateSlug = (title: string): string => {
    return title
      .toLowerCase()
      .replace(/[^a-z0-9\s-]/g, '') //  
      .replace(/\s+/g, '-') //  
      .replace(/-+/g, '-') //   
      .trim()
      + '-' + Date.now(); //    
  };

  const createCourse = async () => {
    try {
      setIsLoading(true);

      // 1.  
      const thumbnailUrl = await uploadThumbnail();

      // 2.    
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error(' ');

      // 3. slug 
      const courseSlug = generateSlug(courseForm.title);

      // 4.  ID 
      const selectedCategory = categories.find(cat => cat.name === courseForm.category);
      
      // 5.  
      const { data: course, error: courseError } = await supabase
        .from('courses')
        .insert({
          title: courseForm.title,
          slug: courseSlug,
          description: courseForm.description,
          short_description: courseForm.description.substring(0, 200),
          instructor_id: user.id,
          category_id: selectedCategory?.id || null,
          level: courseForm.level,
          price: courseForm.price,
          thumbnail_url: thumbnailUrl,
          status: 'draft',
          tags: courseForm.tags,
          learning_outcomes: [],
          language: 'Korean'
        })
        .select()
        .single();

      if (courseError) throw courseError;

      // 4.   
      for (const module of modules) {
        const { data: createdModule, error: moduleError } = await supabase
          .from('modules')
          .insert({
            title: module.title,
            description: module.description,
            course_id: course.id,
            order_index: module.order
          })
          .select()
          .single();

        if (moduleError) throw moduleError;

        //  
        for (const chapter of module.chapters) {
          const { error: chapterError } = await supabase
            .from('chapters')
            .insert({
              title: chapter.title,
              description: chapter.description,
              content_type: chapter.content_type,
              video_url: chapter.video_url,
              content: chapter.text_content,
              duration_minutes: chapter.duration_minutes,
              module_id: createdModule.id,
              order_index: chapter.order
            });

          if (chapterError) throw chapterError;
        }
      }

      alert('  !');
      navigate('/admin');

    } catch (error: any) {
      console.error('  :', error);
      alert('  : ' + error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const validateStep = (step: number): boolean => {
    if (step === 1) {
      return !!(courseForm.title && courseForm.description && courseForm.category);
    }
    if (step === 2) {
      return modules.length > 0 && modules.every(module => 
        module.title && module.chapters.length > 0 && 
        module.chapters.every(chapter => chapter.title)
      );
    }
    return true;
  };

  const nextStep = () => {
    if (validateStep(currentStep)) {
      setCurrentStep(prev => Math.min(prev + 1, 3));
    } else {
      alert('   .');
    }
  };

  const prevStep = () => {
    setCurrentStep(prev => Math.max(prev - 1, 1));
  };

  return (
    <Layout>
      <div className="max-w-4xl mx-auto space-y-6">
        {/*  */}
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-900">  </h1>
          <div className="flex space-x-2">
            <Button variant="secondary" onClick={() => navigate('/admin')}>
              
            </Button>
          </div>
        </div>

        {/*    */}
        <div className="flex items-center justify-center space-x-8 mb-8">
          {[1, 2, 3].map((step) => (
            <div key={step} className="flex items-center">
              <div className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium ${
                currentStep >= step 
                  ? 'bg-indigo-600 text-white' 
                  : 'bg-gray-300 text-gray-500'
              }`}>
                {step}
              </div>
              <span className={`ml-2 text-sm ${
                currentStep >= step ? 'text-indigo-600' : 'text-gray-500'
              }`}>
                {step === 1 ? ' ' : step === 2 ? '' : ''}
              </span>
              {step < 3 && <div className="w-16 h-0.5 bg-gray-300 ml-4" />}
            </div>
          ))}
        </div>

        {/*   */}
        <div className="bg-white rounded-lg shadow p-6">
          {currentStep === 1 && (
            <div className="space-y-6">
              <h2 className="text-xl font-semibold mb-4">  </h2>
              
              <Input
                label="  *"
                name="title"
                value={courseForm.title}
                onChange={handleCourseFormChange}
                placeholder="  "
                required
              />

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                    *
                </label>
                <textarea
                  name="description"
                  value={courseForm.description}
                  onChange={handleCourseFormChange}
                  rows={4}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  placeholder="    "
                  required
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                     *
                  </label>
                  <select
                    name="category"
                    value={courseForm.category}
                    onChange={handleCourseFormChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    required
                  >
                    <option value=""> </option>
                    {categories.map((category) => (
                      <option key={category.id} value={category.name}>
                        {category.icon ? `${category.icon} ${category.name}` : category.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    
                  </label>
                  <select
                    name="level"
                    value={courseForm.level}
                    onChange={handleCourseFormChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  >
                    <option value="beginner"></option>
                    <option value="intermediate"></option>
                    <option value="advanced"></option>
                  </select>
                </div>
              </div>

              <Input
                label="  ()"
                name="price"
                type="number"
                value={courseForm.price}
                onChange={handleCourseFormChange}
                placeholder="0"
                min="0"
              />

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                   
                </label>
                <div className="flex items-center space-x-4">
                  <input
                    type="file"
                    ref={fileInputRef}
                    onChange={handleThumbnailChange}
                    accept="image/*"
                    className="hidden"
                  />
                  <Button
                    type="button"
                    variant="secondary"
                    onClick={() => fileInputRef.current?.click()}
                  >
                     
                  </Button>
                  {thumbnailPreview && (
                    <img
                      src={thumbnailPreview}
                      alt=" "
                      className="w-20 h-20 object-cover rounded-md border"
                    />
                  )}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <div className="flex items-center space-x-2 mb-2">
                  <input
                    type="text"
                    value={newTag}
                    onChange={(e) => setNewTag(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && addTag()}
                    placeholder=" "
                    className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  />
                  <Button type="button" onClick={addTag} variant="secondary">
                    
                  </Button>
                </div>
                <div className="flex flex-wrap gap-2">
                  {courseForm.tags.map((tag, index) => (
                    <span
                      key={index}
                      className="inline-flex items-center px-3 py-1 rounded-full text-sm bg-indigo-100 text-indigo-800"
                    >
                      {tag}
                      <button
                        type="button"
                        onClick={() => removeTag(index)}
                        className="ml-2 text-indigo-600 hover:text-indigo-800"
                      >
                        
                      </button>
                    </span>
                  ))}
                </div>
              </div>
            </div>
          )}

          {currentStep === 2 && (
            <div className="space-y-6">
              <div className="flex justify-between items-center">
                <h2 className="text-xl font-semibold"> </h2>
                <Button onClick={addModule} variant="primary">
                   
                </Button>
              </div>

              {modules.map((module, moduleIndex) => (
                <div key={module.id} className="border border-gray-200 rounded-lg p-4">
                  <div className="flex justify-between items-start mb-4">
                    <h3 className="text-lg font-medium"> {moduleIndex + 1}</h3>
                    <Button
                      onClick={() => removeModule(module.id)}
                      variant="secondary"
                      className="text-red-600 hover:text-red-800"
                    >
                      
                    </Button>
                  </div>

                  <div className="space-y-4">
                    <Input
                      label="  *"
                      value={module.title}
                      onChange={(e) => updateModule(module.id, 'title', e.target.value)}
                      placeholder=" "
                      required
                    />

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                         
                      </label>
                      <textarea
                        value={module.description}
                        onChange={(e) => updateModule(module.id, 'description', e.target.value)}
                        rows={2}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                        placeholder=" "
                      />
                    </div>

                    <div className="bg-gray-50 p-4 rounded-md">
                      <div className="flex justify-between items-center mb-3">
                        <h4 className="font-medium"></h4>
                        <Button
                          onClick={() => addChapter(module.id)}
                          variant="secondary"
                          size="sm"
                        >
                           
                        </Button>
                      </div>

                      {module.chapters.map((chapter, chapterIndex) => (
                        <div key={chapter.id} className="bg-white p-3 rounded border mb-3">
                          <div className="flex justify-between items-start mb-3">
                            <span className="text-sm font-medium"> {chapterIndex + 1}</span>
                            <Button
                              onClick={() => removeChapter(module.id, chapter.id)}
                              variant="secondary"
                              size="sm"
                              className="text-red-600 hover:text-red-800"
                            >
                              
                            </Button>
                          </div>

                          <div className="space-y-3">
                            <Input
                              label="  *"
                              value={chapter.title}
                              onChange={(e) => updateChapter(module.id, chapter.id, 'title', e.target.value)}
                              placeholder=" "
                              required
                            />

                            <div>
                              <label className="block text-sm font-medium text-gray-700 mb-1">
                                 
                              </label>
                              <select
                                value={chapter.content_type}
                                onChange={(e) => updateChapter(module.id, chapter.id, 'content_type', e.target.value)}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                              >
                                <option value="video"></option>
                                <option value="text"></option>
                                <option value="pdf">PDF</option>
                                <option value="quiz"></option>
                              </select>
                            </div>

                            {chapter.content_type === 'video' && (
                              <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                   
                                </label>
                                <input
                                  type="file"
                                  accept="video/*"
                                  onChange={(e) => {
                                    const file = e.target.files?.[0];
                                    if (file) {
                                      handleVideoUpload(module.id, chapter.id, file);
                                    }
                                  }}
                                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                                />
                                {chapter.video_url && (
                                  <div className="mt-2 space-y-1">
                                    <p className="text-sm text-green-600">
                                        
                                    </p>
                                    {chapter.duration_minutes > 0 && (
                                      <p className="text-sm text-blue-600">
                                          : {chapter.duration_minutes}
                                      </p>
                                    )}
                                  </div>
                                )}
                              </div>
                            )}

                            <div>
                              <label className="block text-sm font-medium text-gray-700 mb-1">
                                 
                              </label>
                              <textarea
                                value={chapter.description}
                                onChange={(e) => updateChapter(module.id, chapter.id, 'description', e.target.value)}
                                rows={2}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500"
                                placeholder="  ()"
                              />
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}

          {currentStep === 3 && (
            <div className="space-y-6">
              <h2 className="text-xl font-semibold mb-4"> </h2>
              
              <div className="bg-gray-50 p-6 rounded-lg">
                <h3 className="text-lg font-semibold mb-2">{courseForm.title}</h3>
                <p className="text-gray-600 mb-4">{courseForm.description}</p>
                
                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <span className="text-sm font-medium text-gray-500">: </span>
                    <span>{courseForm.category}</span>
                  </div>
                  <div>
                    <span className="text-sm font-medium text-gray-500">: </span>
                    <span>{courseForm.level === 'beginner' ? '' : courseForm.level === 'intermediate' ? '' : ''}</span>
                  </div>
                  <div>
                    <span className="text-sm font-medium text-gray-500">: </span>
                    <span>{courseForm.price.toLocaleString()}</span>
                  </div>
                  <div>
                    <span className="text-sm font-medium text-gray-500"> : </span>
                    <span>{modules.length}</span>
                  </div>
                </div>

                {courseForm.tags.length > 0 && (
                  <div className="mb-4">
                    <span className="text-sm font-medium text-gray-500">: </span>
                    {courseForm.tags.map((tag, index) => (
                      <span key={index} className="inline-block bg-indigo-100 text-indigo-800 px-2 py-1 rounded text-sm mr-2">
                        {tag}
                      </span>
                    ))}
                  </div>
                )}

                <div className="space-y-3">
                  <h4 className="font-medium"></h4>
                  {modules.map((module, moduleIndex) => (
                    <div key={module.id} className="bg-white p-3 rounded border">
                      <h5 className="font-medium"> {moduleIndex + 1}: {module.title}</h5>
                      <p className="text-sm text-gray-600 mb-2">{module.description}</p>
                      <ul className="text-sm space-y-1">
                        {module.chapters.map((chapter, chapterIndex) => (
                          <li key={chapter.id} className="flex justify-between">
                            <span> {chapter.title}</span>
                            <span className="text-gray-500">{chapter.duration_minutes}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>

        {/*   */}
        <div className="flex justify-between">
          <div className="flex space-x-2">
            <Button
              onClick={prevStep}
              variant="secondary"
              disabled={currentStep === 1}
            >
              
            </Button>
            
            <Button
              onClick={() => setShowStorageGuide(true)}
              variant="secondary"
              className="bg-orange-100 text-orange-700 hover:bg-orange-200"
            >
               Storage  
            </Button>
          </div>

          <div className="flex space-x-2">
            {currentStep < 3 ? (
              <Button onClick={nextStep} variant="primary">
                
              </Button>
            ) : (
              <Button
                onClick={createCourse}
                variant="primary"
                isLoading={isLoading}
              >
                 
              </Button>
            )}
          </div>
        </div>
      </div>
      
      {/* Storage    */}
      {showStorageGuide && (
        <StorageSetupGuide onClose={() => setShowStorageGuide(false)} />
      )}
    </Layout>
  );
};

export default CourseCreator;