import React from 'react';
import { Navigate, RouteObject } from 'react-router-dom';

//  
import Login from './features/auth/Login';
import Register from './features/auth/Register';
import EnhancedRegister from './features/auth/EnhancedRegister';

// 
import Dashboard from './features/dashboard/Dashboard';

//  
import CourseList from './features/courses/CourseList';
import CourseDetail from './features/courses/CourseDetail';

//  
import SubscriptionPage from './features/subscription/SubscriptionPage';

//  
import PaymentComplete from './features/payment/PaymentComplete';
import PaymentCancel from './features/payment/PaymentCancel';

//  
import Profile from './features/profile/Profile';
import AdminDashboard from './features/admin/AdminDashboard';
import CourseCreator from './features/admin/CourseCreator';
import InstructorDashboard from './features/instructor/InstructorDashboard';
import ProtectedRoute from './components/common/ProtectedRoute';
const NotFound = () => <div>404 - Page Not Found</div>;

//   
export const createRoutes = (isAuthenticated: boolean, userRole: string): RouteObject[] => {
  return [
    //  
    {
      path: '/login',
      element: isAuthenticated ? <Navigate to="/dashboard" replace /> : <Login />,
    },
    {
      path: '/register',
      element: isAuthenticated ? <Navigate to="/dashboard" replace /> : <EnhancedRegister />,
    },
    {
      path: '/register-old',
      element: isAuthenticated ? <Navigate to="/dashboard" replace /> : <Register />,
    },

    //  
    {
      path: '/dashboard',
      element: (
        <ProtectedRoute>
          <Dashboard />
        </ProtectedRoute>
      ),
    },
    {
      path: '/courses',
      element: (
        <ProtectedRoute>
          <CourseList />
        </ProtectedRoute>
      ),
    },
    {
      path: '/courses/:courseId',
      element: (
        <ProtectedRoute>
          <CourseDetail />
        </ProtectedRoute>
      ),
    },
    {
      path: '/profile',
      element: (
        <ProtectedRoute>
          <Profile />
        </ProtectedRoute>
      ),
    },
    {
      path: '/subscription',
      element: (
        <ProtectedRoute>
          <SubscriptionPage />
        </ProtectedRoute>
      ),
    },
    {
      path: '/payment/complete',
      element: (
        <ProtectedRoute>
          <PaymentComplete />
        </ProtectedRoute>
      ),
    },
    {
      path: '/payment/cancel',
      element: (
        <ProtectedRoute>
          <PaymentCancel />
        </ProtectedRoute>
      ),
    },

    //   
    {
      path: '/my-courses',
      element: (
        <ProtectedRoute allowedRoles={['instructor', 'admin']}>
          <InstructorDashboard />
        </ProtectedRoute>
      ),
    },

    //   
    {
      path: '/admin',
      element: (
        <ProtectedRoute requiredRole="admin">
          <AdminDashboard />
        </ProtectedRoute>
      ),
    },
    {
      path: '/admin/course/create',
      element: (
        <ProtectedRoute requiredRole="admin">
          <CourseCreator />
        </ProtectedRoute>
      ),
    },

    // 
    {
      path: '/',
      element: <Navigate to={isAuthenticated ? (userRole === 'admin' ? '/admin' : '/dashboard') : '/login'} replace />,
    },

    //    
    {
      path: '*',
      element: <NotFound />,
    },
  ];
};

export default createRoutes;