import React, { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useAppSelector } from '../../app/store';
import paymentService from '../../services/paymentService';

interface PaymentResult {
  success: boolean;
  message: string;
  orderId?: string;
  amount?: number;
  subscription?: {
    plan_name: string;
    valid_until: string;
  };
}

const PaymentComplete: React.FC = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { user } = useAppSelector((state) => state.auth);
  const [result, setResult] = useState<PaymentResult | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const verifyPayment = async () => {
      try {
        // URL      
        const resultCode = searchParams.get('resultCode');
        const resultMsg = searchParams.get('resultMsg');
        const tid = searchParams.get('tid');
        const orderId = searchParams.get('orderId');
        const amount = searchParams.get('amount');
        const payMethod = searchParams.get('payMethod');
        const mallReserved = searchParams.get('mallReserved');

        if (resultCode === '0000') {
          //  
          const paymentResult = {
            success: true,
            paymentId: tid || undefined,
            orderId: orderId || undefined,
            amount: amount ? parseInt(amount) : 0,
            method: payMethod || undefined,
            data: {
              mallReserved,
              tid,
              resultCode,
              resultMsg
            }
          };

          const isCompleted = await paymentService.completePayment(paymentResult);
          
          if (isCompleted) {
            setResult({
              success: true,
              message: '  .',
              orderId: orderId || undefined,
              amount: paymentResult.amount
            });

            //     
            const parsedMallReserved = mallReserved ? JSON.parse(mallReserved) : {};
            
            setTimeout(() => {
              if (parsedMallReserved.paymentType === 'course') {
                navigate(`/courses/${parsedMallReserved.courseId}`);
              } else if (parsedMallReserved.paymentType === 'subscription') {
                navigate('/dashboard');
              } else {
                navigate('/dashboard');
              }
            }, 3000);
          } else {
            throw new Error('     .');
          }
        } else {
          //  
          setResult({
            success: false,
            message: resultMsg || ' .',
            orderId: orderId || undefined
          });

          setTimeout(() => {
            navigate('/dashboard');
          }, 3000);
        }
      } catch (error: any) {
        console.error('Payment verification error:', error);
        setResult({
          success: false,
          message: error.message || '    .'
        });

        setTimeout(() => {
          navigate('/dashboard');
        }, 3000);
      } finally {
        setLoading(false);
      }
    };

    verifyPayment();
  }, [searchParams, navigate]);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-indigo-600 mx-auto"></div>
          <p className="mt-4 text-lg text-gray-600">  ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50">
      <div className="max-w-md w-full bg-white rounded-lg shadow-md p-6 text-center">
        {result?.success ? (
          <>
            <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg className="w-8 h-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7"></path>
              </svg>
            </div>
            <h2 className="text-2xl font-bold text-gray-900 mb-2"> </h2>
            <p className="text-gray-600 mb-4">{result.message}</p>
            {result.subscription && (
              <div className="bg-gray-50 rounded-lg p-4 mb-4">
                <p className="text-sm text-gray-600"> </p>
                <p className="font-semibold">{result.subscription.plan_name}</p>
                <p className="text-sm text-gray-600 mt-2"></p>
                <p className="font-semibold">{new Date(result.subscription.valid_until).toLocaleDateString()}</p>
              </div>
            )}
            <button
              onClick={() => navigate('/dashboard')}
              className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-medium py-2 px-4 rounded-md transition-colors"
            >
               
            </button>
          </>
        ) : (
          <>
            <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
              </svg>
            </div>
            <h2 className="text-2xl font-bold text-gray-900 mb-2"> </h2>
            <p className="text-gray-600 mb-4">{result?.message}</p>
            <div className="space-y-2">
              <button
                onClick={() => navigate('/subscription')}
                className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-medium py-2 px-4 rounded-md transition-colors"
              >
                 
              </button>
              <button
                onClick={() => navigate('/dashboard')}
                className="w-full bg-gray-300 hover:bg-gray-400 text-gray-700 font-medium py-2 px-4 rounded-md transition-colors"
              >
                 
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default PaymentComplete;