import { supabase } from '../lib/supabase';

// Supabase Storage    
export const setupStorageBuckets = async () => {
  try {
    console.log(' Storage   ...');

    //     
    const { data: existingBuckets, error: listError } = await supabase.storage.listBuckets();
    if (listError) {
      console.error('    :', listError);
      return { success: false, error: listError };
    }

    console.log('  :', existingBuckets.map(b => b.name));

    // 1. course-videos  
    const hasVideoBucket = existingBuckets.some(bucket => bucket.name === 'course-videos');
    
    if (!hasVideoBucket) {
      console.log(' course-videos   ...');
      const { data: videoBucket, error: videoBucketError } = await supabase.storage
        .createBucket('course-videos', {
          public: true,
          allowedMimeTypes: ['video/mp4', 'video/webm', 'video/ogg', 'video/avi', 'video/mov', 'video/quicktime'],
          fileSizeLimit: 1024 * 1024 * 500 // 500MB
        });

      if (videoBucketError) {
        console.error(' course-videos   :', videoBucketError);
        //     
        if (!videoBucketError.message.includes('already exists') && !videoBucketError.message.includes('Duplicate')) {
          throw videoBucketError;
        }
      }
      console.log(' course-videos   :', videoBucket);
    } else {
      console.log(' course-videos   ');
    }

    // 2. course-thumbnails  
    const hasThumbnailBucket = existingBuckets.some(bucket => bucket.name === 'course-thumbnails');
    
    if (!hasThumbnailBucket) {
      console.log(' course-thumbnails   ...');
      const { data: thumbnailBucket, error: thumbnailBucketError } = await supabase.storage
        .createBucket('course-thumbnails', {
          public: true,
          allowedMimeTypes: ['image/jpeg', 'image/png', 'image/webp'],
          fileSizeLimit: 1024 * 1024 * 10 // 10MB
        });

      if (thumbnailBucketError) {
        console.error(' course-thumbnails   :', thumbnailBucketError);
        if (!thumbnailBucketError.message.includes('already exists') && !thumbnailBucketError.message.includes('Duplicate')) {
          throw thumbnailBucketError;
        }
      }
      console.log(' course-thumbnails   :', thumbnailBucket);
    } else {
      console.log(' course-thumbnails   ');
    }

    // 3.    
    const { data: finalBuckets, error: finalListError } = await supabase.storage.listBuckets();
    
    if (finalListError) {
      console.warn('     :', finalListError);
    } else {
      console.log('  Storage :');
      finalBuckets.forEach(bucket => {
        console.log(`  - ${bucket.name} (public: ${bucket.public})`);
      });
    }

    return { success: true, buckets: finalBuckets };
  } catch (error) {
    console.error(' Storage   :', error);
    return { success: false, error };
  }
};

//  URL  
export const validateVideoUrl = async (url: string): Promise<boolean> => {
  try {
    const response = await fetch(url, { method: 'HEAD' });
    return response.ok;
  } catch (error) {
    console.error(' URL  :', error);
    return false;
  }
};

// Signed URL  ( )
export const getSignedVideoUrl = async (filePath: string, expiresIn = 3600) => {
  try {
    const { data, error } = await supabase.storage
      .from('course-videos')
      .createSignedUrl(filePath, expiresIn);
    
    if (error) throw error;
    
    return data.signedUrl;
  } catch (error) {
    console.error('Signed URL  :', error);
    return null;
  }
};