import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Layout from '../../components/common/Layout';
import { useAppSelector } from '../../app/store';
import { apiClient, supabaseApi } from '../../api/apiClient';
import courseImages from '../../data/courseImages';

// Supabase  
interface Course {
  id: string;
  title: string;
  instructor: string;
  progress: number;
  lastAccessed: string;
  imageUrl: string;
  imageAlt?: string;
  enrollment_id?: string;
}

const Dashboard: React.FC = () => {
  const { user } = useAppSelector((state) => state.auth);
  const [enrolledCourses, setEnrolledCourses] = useState<Course[]>([]);
  const [recommendedCourses, setRecommendedCourses] = useState<Course[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchDashboardData = async () => {
      try {
        if (!user) {
          setIsLoading(false);
          return;
        }

        console.log(' Dashboard: Fetching data from Supabase...');

        //  Supabase API 
        const { data: enrollments, error: enrollmentError } = await supabaseApi.enrollment.getUserEnrollments(user.id);
        
        if (enrollmentError) {
          console.error('Error fetching enrollments:', enrollmentError);
          setIsLoading(false);
          return;
        }

        //     
        const enrolledCoursesData: Course[] = (enrollments || []).map((enrollment: any) => ({
          id: enrollment.course.id,
          title: enrollment.course.title,
          instructor: enrollment.course.instructor ? 
            `${enrollment.course.instructor.first_name || ''} ${enrollment.course.instructor.last_name || ''}`.trim() ||
            enrollment.course.instructor.email : '  ',
          progress: enrollment.progress || 0,
          lastAccessed: new Date(enrollment.enrollment_date).toLocaleDateString(),
          imageUrl: enrollment.course.thumbnail_url || courseImages.python.placeholder,
          imageAlt: enrollment.course.title,
          enrollment_id: enrollment.id,
        }));

        setEnrolledCourses(enrolledCoursesData);

        //    (      )
        const { data: allCourses, error: coursesError } = await supabaseApi.course.getCourses();
        
        if (!coursesError && allCourses) {
          const enrolledCourseIds = enrolledCoursesData.map(course => course.id);
          const recommendedCoursesData: Course[] = allCourses
            .filter((course: any) => !enrolledCourseIds.includes(course.id))
            .slice(0, 4) //  4
            .map((course: any) => ({
              id: course.id,
              title: course.title,
              instructor: course.instructor ? 
                `${course.instructor.first_name || ''} ${course.instructor.last_name || ''}`.trim() ||
                course.instructor.email : '  ',
              progress: 0,
              lastAccessed: '',
              imageUrl: course.thumbnail_url || courseImages.webDev.placeholder,
              imageAlt: course.title,
            }));

          setRecommendedCourses(recommendedCoursesData);
        }

        setIsLoading(false);
      } catch (error) {
        console.error('Error fetching dashboard data:', error);
        setIsLoading(false);
      }
    };

    fetchDashboardData();
  }, [user]);

  //   
  const ProgressBar: React.FC<{ progress: number }> = ({ progress }) => (
    <div className="w-full bg-gray-200 rounded-full h-2.5">
      <div
        className="bg-indigo-600 h-2.5 rounded-full"
        style={{ width: `${progress}%` }}
      ></div>
    </div>
  );

  //   
  const CourseCard: React.FC<{
    course: Course;
    showProgress?: boolean;
  }> = ({ course, showProgress = false }) => (
    <div className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-all duration-300 transform hover:-translate-y-1 hover:scale-[1.01]">
      <Link to={`/courses/${course.id}`}>
        <img
          src={course.imageUrl}
          alt={course.imageAlt || course.title}
          className="w-full h-40 object-cover rounded-t-lg"
        />
        <div className="p-4">
          <h3 className="font-bold text-lg mb-1">{course.title}</h3>
          <p className="text-gray-600 text-sm mb-2">: {course.instructor}</p>
          
          {showProgress && (
            <>
              <div className="flex justify-between text-sm text-gray-500 mb-1">
                <span></span>
                <span>{course.progress}%</span>
              </div>
              <ProgressBar progress={course.progress} />
              {course.lastAccessed && (
                <p className="text-xs text-gray-500 mt-2">
                   : {course.lastAccessed}
                </p>
              )}
            </>
          )}
          
          {!showProgress && (
            <div className="mt-2">
              <span className="inline-block bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded">
                
              </span>
            </div>
          )}
        </div>
      </Link>
    </div>
  );

  //   
  if (isLoading) {
    return (
      <Layout>
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <div>
        {/*   */}
        <div className="bg-gradient-to-r from-indigo-500 to-purple-600 text-white rounded-lg p-6 mb-8">
          <h1 className="text-2xl font-bold mb-2">
            , {user?.first_name || ''}!
          </h1>
          <p> AI-LMS   .</p>
        </div>

        {/*    */}
        <div className="mb-10">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold">  </h2>
            <Link to="/courses" className="text-indigo-600 hover:text-indigo-800">
               
            </Link>
          </div>
          
          {enrolledCourses.length === 0 ? (
            <div className="bg-gray-50 rounded-lg p-6 text-center">
              <p className="text-gray-600 mb-4">    .</p>
              <Link
                to="/courses"
                className="inline-block bg-indigo-600 text-white px-4 py-2 rounded hover:bg-indigo-700"
              >
                 
              </Link>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {enrolledCourses.map((course) => (
                <CourseCard key={course.id} course={course} showProgress={true} />
              ))}
            </div>
          )}
        </div>

        {/*   */}
        <div className="mb-10">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold">AI  </h2>
            <Link to="/recommendations" className="text-indigo-600 hover:text-indigo-800">
                
            </Link>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {recommendedCourses.map((course) => (
              <CourseCard key={course.id} course={course} />
            ))}
          </div>
        </div>

        {/*   */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-bold mb-4"> </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-gray-50 p-4 rounded">
              <p className="text-gray-600 text-sm">   </p>
              <p className="text-2xl font-bold">12 30</p>
            </div>
            <div className="bg-gray-50 p-4 rounded">
              <p className="text-gray-600 text-sm"> </p>
              <p className="text-2xl font-bold">7</p>
            </div>
            <div className="bg-gray-50 p-4 rounded">
              <p className="text-gray-600 text-sm"> </p>
              <p className="text-2xl font-bold">5</p>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Dashboard;