import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../app/store';
import { login, clearError } from './authSlice';
import Input from '../../components/common/Input';
import Button from '../../components/common/Button';
import Layout from '../../components/common/Layout';

const Login: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [formErrors, setFormErrors] = useState({
    email: '',
    password: '',
  });

  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { isLoading, error, isAuthenticated, user } = useAppSelector((state) => state.auth);

  useEffect(() => {
    //    role  
    if (isAuthenticated && user) {
      if (user.role === 'admin') {
        //  React   
        navigate('/admin');
      } else {
        navigate('/dashboard');
      }
    }
    
    //     
    return () => {
      dispatch(clearError());
    };
  }, [isAuthenticated, user, navigate, dispatch]);

  const validateForm = (): boolean => {
    let valid = true;
    const newErrors = { email: '', password: '' };
    
    if (!email.trim()) {
      newErrors.email = ' ';
      valid = false;
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      newErrors.email = '   ';
      valid = false;
    }
    
    if (!password) {
      newErrors.password = ' ';
      valid = false;
    } else if (password.length < 6) {
      newErrors.password = '  6  ';
      valid = false;
    }
    
    setFormErrors(newErrors);
    return valid;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      try {
        console.log('  :', { email, password: '***' });
        
        //  Supabase  
        const result = await dispatch(login({ email, password })).unwrap();
        console.log('  :', result);
        
        // role  
        if (result.user.role === 'admin') {
          navigate('/admin');
        } else {
          navigate('/dashboard');
        }
        
      } catch (err: any) {
        console.error('  :', err);
        
        //     
        let errorMessage = ' .';
        
        if (err.message) {
          if (err.message.includes('Invalid login credentials')) {
            errorMessage = '    .';
          } else if (err.message.includes('Email not confirmed')) {
            errorMessage = '   .  .';
          } else if (err.message.includes('Invalid email')) {
            errorMessage = '   .';
          } else if (err.message.includes('Too many requests')) {
            errorMessage = '    .    .';
          } else {
            errorMessage += `\n: ${err.message}`;
          }
        }
        
        //   
        errorMessage += '\n\n      .';
        
        alert(errorMessage);
        
        //  authSlice extraReducers 
      }
    }
  };

  return (
    <Layout>
      <div className="max-w-md mx-auto">
        <h1 className="text-2xl font-bold mb-6 text-center"></h1>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}
        
        {/*     */}
        {process.env.NODE_ENV === 'development' && (
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-4">
            <h3 className="text-sm font-medium text-blue-900 mb-2">  -  </h3>
            <div className="space-y-2 text-sm">
              <button
                type="button"
                onClick={() => {
                  setEmail('admin@test.com');
                  setPassword('test123');
                }}
                className="block w-full text-left px-2 py-1 hover:bg-blue-100 rounded"
              >
                 : admin@test.com / test123
              </button>
              <button
                type="button"
                onClick={() => {
                  setEmail('instructor@test.com');
                  setPassword('test123');
                }}
                className="block w-full text-left px-2 py-1 hover:bg-blue-100 rounded"
              >
                 : instructor@test.com / test123
              </button>
              <button
                type="button"
                onClick={() => {
                  setEmail('student@test.com');
                  setPassword('test123');
                }}
                className="block w-full text-left px-2 py-1 hover:bg-blue-100 rounded"
              >
                 : student@test.com / test123
              </button>
            </div>
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-4">
          <Input
            id="email"
            name="email"
            type="email"
            label=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder=" "
            error={formErrors.email}
            fullWidth
          />
          
          <Input
            id="password"
            name="password"
            type="password"
            label=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder=" "
            error={formErrors.password}
            fullWidth
          />
          
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              <input
                id="remember-me"
                name="remember-me"
                type="checkbox"
                className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
              />
              <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-700">
                  
              </label>
            </div>
            
            <div className="text-sm">
              <Link to="/forgot-password" className="text-indigo-600 hover:text-indigo-500">
                 ?
              </Link>
            </div>
          </div>
          
          <Button
            type="submit"
            variant="primary"
            isLoading={isLoading}
            fullWidth
          >
            
          </Button>
          
          <div className="text-center mt-4">
            <span className="text-gray-600"> ?</span>{' '}
            <Link to="/register" className="text-indigo-600 hover:text-indigo-500">
              
            </Link>
          </div>
        </form>
      </div>
    </Layout>
  );
};

export default Login;