import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import apiClient, { supabaseApi } from '../../api/apiClient';
import { jwtDecode } from 'jwt-decode';
import type { UserProfile } from '../../lib/supabase';

//   (Supabase )
interface User {
  id: string; // UUID 
  email: string;
  first_name?: string;
  last_name?: string;
  role: 'student' | 'instructor' | 'admin';
  profile?: UserProfile; // Supabase  
}

interface AuthState {
  token: string | null;
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  error: string | null;
}

interface LoginCredentials {
  email: string;
  password: string;
}

interface RegisterCredentials {
  email: string;
  password: string;
  password_confirm: string;
  first_name: string;
  last_name: string;
  role: 'student' | 'instructor' | 'admin';
  phone_number?: string;
  address?: string;
  organization?: string;
  job_title?: string;
  bio?: string;
}

interface LoginResponse {
  user: User;
  token?: string;
  session?: any; // Supabase  
}

// localStorage   
const getInitialAuthState = (): AuthState => {
  try {
    const token = localStorage.getItem('token');
    const userString = localStorage.getItem('user');
    
    if (token && userString) {
      const user = JSON.parse(userString);
      return {
        token,
        user,
        isAuthenticated: true,
        isLoading: false,
        error: null,
      };
    }
  } catch (error) {
    console.error('     :', error);
    //   localStorage 
    localStorage.removeItem('token');
    localStorage.removeItem('user');
  }
  
  return {
    token: null,
    user: null,
    isAuthenticated: false,
    isLoading: false,
    error: null,
  };
};

//  
const initialState: AuthState = getInitialAuthState();

//    (Supabase )
export const login = createAsyncThunk(
  'auth/login',
  async (credentials: LoginCredentials, { rejectWithValue }) => {
    try {
      const response = await apiClient.auth.login(credentials.email, credentials.password);
      
      // Supabase   localStorage  (, Supabase  )
      if (response.data.token) {
        localStorage.setItem('token', response.data.token);
      }
      
      return response.data;
    } catch (error: any) {
      console.error('Login error:', error);
      return rejectWithValue(error.message || 'Login failed');
    }
  }
);

//    (Supabase )
export const register = createAsyncThunk(
  'auth/register',
  async (credentials: RegisterCredentials, { rejectWithValue }) => {
    try {
      if (credentials.password !== credentials.password_confirm) {
        return rejectWithValue('Passwords do not match');
      }
      
      const response = await apiClient.auth.register(
        credentials.email,
        credentials.password,
        credentials.first_name,
        credentials.last_name,
        credentials.role,
        {
          phone_number: credentials.phone_number,
          address: credentials.address,
          organization: credentials.organization,
          job_title: credentials.job_title,
          bio: credentials.bio,
        }
      );
      
      return response.data;
    } catch (error: any) {
      console.error('Registration error:', error);
      return rejectWithValue(error.message || 'Registration failed');
    }
  }
);

//     (Supabase )
export const getUserProfile = createAsyncThunk(
  'auth/getUserProfile',
  async (_, { rejectWithValue }) => {
    try {
      const response = await apiClient.auth.getCurrentUser();
      
      if (!response.data) {
        return rejectWithValue('No user found');
      }
      
      return response.data;
    } catch (error: any) {
      console.error('Profile fetch error:', error);
      return rejectWithValue(error.message || 'Failed to fetch user profile');
    }
  }
);

//    (Supabase )
export const checkSession = createAsyncThunk(
  'auth/checkSession',
  async (_, { rejectWithValue }) => {
    try {
      const session = await supabaseApi.auth.getSession();
      if (session?.user) {
        const { data: profile } = await supabaseApi.user.getProfile(session.user.id);
        return {
          user: {
            id: session.user.id,
            email: session.user.email || '',
            first_name: profile?.first_name,
            last_name: profile?.last_name,
            role: profile?.role || 'student',
            profile
          },
          token: session.access_token
        };
      }
      return null;
    } catch (error: any) {
      console.error('Session check error:', error);
      return rejectWithValue(error.message || 'Session check failed');
    }
  }
);

// Auth 
const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    logout: (state) => {
      // Supabase  
      supabaseApi.auth.signOut();
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      state.token = null;
      state.user = null;
      state.isAuthenticated = false;
      state.error = null;
    },
    clearError: (state) => {
      state.error = null;
    },
    setAuthenticatedUser: (state, action: PayloadAction<{ user: User; token?: string }>) => {
      state.user = action.payload.user;
      state.token = action.payload.token || null;
      state.isAuthenticated = true;
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    //   
    builder
      .addCase(login.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(login.fulfilled, (state, action: PayloadAction<LoginResponse>) => {
        state.isLoading = false;
        state.isAuthenticated = true;
        state.token = action.payload.token || null;
        state.user = action.payload.user;
        
        // localStorage 
        if (action.payload.token) {
          localStorage.setItem('token', action.payload.token);
        }
        localStorage.setItem('user', JSON.stringify(action.payload.user));
      })
      .addCase(login.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload as string;
      });
    
    //   
    builder
      .addCase(register.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(register.fulfilled, (state) => {
        state.isLoading = false;
      })
      .addCase(register.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload as string;
      });
    
    //     
    builder
      .addCase(getUserProfile.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(getUserProfile.fulfilled, (state, action: PayloadAction<User>) => {
        state.isLoading = false;
        state.user = action.payload;
      })
      .addCase(getUserProfile.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload as string;
        //    
        const errorMessage = action.payload as string;
        if (errorMessage?.includes('not authenticated') || errorMessage?.includes('No user found')) {
          state.token = null;
          state.user = null;
          state.isAuthenticated = false;
          localStorage.removeItem('token');
        }
      });
    
    //    
    builder
      .addCase(checkSession.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(checkSession.fulfilled, (state, action) => {
        state.isLoading = false;
        if (action.payload) {
          state.isAuthenticated = true;
          state.token = action.payload.token;
          state.user = {
            ...action.payload.user,
            profile: action.payload.user.profile || undefined
          };
          
          // localStorage 
          if (action.payload.token) {
            localStorage.setItem('token', action.payload.token);
          }
          localStorage.setItem('user', JSON.stringify(state.user));
        }
      })
      .addCase(checkSession.rejected, (state, action) => {
        state.isLoading = false;
        state.isAuthenticated = false;
        state.token = null;
        state.user = null;
      });
  },
});

export const { logout, clearError, setAuthenticatedUser } = authSlice.actions;
export default authSlice.reducer;